<template lang="pug">
transition(name="slide-fade" mode="out-in")
  ul.container-card.cs(v-if="!$store.state.loading")
      li(v-for="carrera in carreras")
        router-link(:to="{ name: i, params: {carrera: carrera.f, titulo: carrera.c } }") {{carrera.c}}
</template>
<script>

import axios from 'axios'

export default {
  name: 'Carreras',
  components: {
    
  },
  data() {
    return{
      carreras: {}
    }
  },
  props: {
    f: String,
    i: String
  },
  beforeMount(){
    this.$store.commit('startLoading')
  },
  mounted() {
  	axios
      .get("assets/json/carreras/"+this.f+".json")
      .then(response => (this.carreras = response.data,this.$store.commit('stopLoading')))
  }
}
</script>
<style lang="stylus">

ul.cs

  li
    font-size:150%
    padding: .5rem .75rem

</style>